import { Component, ViewChild }                                            from '@angular/core';
import { IonicPage, NavController, NavParams, MenuController , Content, Platform, AlertController}  from 'ionic-angular';
import { Cities, Developments, Properties, User}                                                from '../../providers';
import {  DevelopmentsPage, PropertiesPage}                                             from '..';


@IonicPage()
@Component({
  selector: 'page-cities',
  templateUrl: 'cities.html',
})
export class CitiesPage {
  @ViewChild(Content) content: Content; /** Controla la pagina */
  

  btnCities = false; 
  values : any = []; 
  title : any ;
  public type : any;
  constructor(private alertCtrl: AlertController,public platform : Platform, public cities : Cities, public menu: MenuController, private navCtrl: NavController, public navParams: NavParams, private developments: Developments, private properties: Properties, public user : User) {

    
    this.menu.enable(true);

    console.log('constructor')

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad')


    if (this.cities.flag == 1) {

      this.values = this.cities.cities;
      this.title = 'Desarrollos';
      this.type = 'Desarrollos';
    } else if(this.cities.flag == 2) {
      this.values = this.cities.citiesLuxuri;

      this.title = 'Propiedades';
      this.type = 'Propiedades';
    } else  {
      
      this.title = 'MI inventario';
      this.type = 'Propiedades';
      this.values = this.cities.citiesEnlace;
    }

  
  }


  ionViewDidEnter() { 
    console.log('ionViewDidEnter')

    this.menu.enable(true);

  }

  go(stateid){
    console.log('go')

    console.log('abriendo pagina')
    if (this.btnCities)
      return;

    this.btnCities = true;


    if(this.cities.flag == 1){
      this.navCtrl.push(DevelopmentsPage, stateid)
      this.developments.init(this.user.userInfo.companyid);
      this.btnCities = false;
    }else if(this.cities.flag == 2) {
      this.navCtrl.push(PropertiesPage, stateid);
      this.properties.init(this.user.userInfo.companyid);
      this.btnCities = false;
    }else  {
      this.navCtrl.push(PropertiesPage, stateid);
      this.properties.propertiesEnlace(this.user.userInfo.companyid);
      this.btnCities = false;
    }
  
  }

  filtre(){
    console.log('filtre')

    this.navCtrl.push('FiltrePage', this.cities.flag);
  }

}



